import axios from 'axios';
import React, { Component } from 'react';
import './App.css';
import Form from './components/Form';
import WeatherPanel from './components/WeatherPanel';


//https://www.metaweather.com/api/location/523920/
//Kelwiny 273.15

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      location: "Wrocław",
      locationId: "3081366",
      weather: ["init"],
      error: ""
    }
  }
  
  componentDidMount(){
    axios.get(`http://api.openweathermap.org/data/2.5/forecast?id=${this.state.locationId}&appid=c9a9dab97a39c92e2fb1365560169a74`)
    .then(res => res.data.list)
    .then(list => this.setState({weather: list}))
    .catch(error => console.log(error));
  }

  handleGetForecastButtonClick = (e) => {
    axios.get(`http://api.openweathermap.org/data/2.5/forecast?id=${this.state.locationId}&appid=c9a9dab97a39c92e2fb1365560169a74`)
      .then(res => res.data.list)
      .then(list => this.setState({weather: list}))
      .catch(error => console.log(error));

      //console.log(this.state.weather);
  }  

  handleChangeInputValue = (e) => {
    console.log(e.target.value);
    this.setState({
      locationId: e.target.value
    });
    
    axios.get(`http://api.openweathermap.org/data/2.5/forecast?id=${this.state.locationId}&appid=c9a9dab97a39c92e2fb1365560169a74`)
    .then(res => res.data.list)
    .then(list => this.setState({weather: list}))
    .catch(error => console.log(error));
  }



  render() {
    return (
      <div className="container">
        <div className="row justify-content-center top-buffer">
          <div className="col-md-8">
          <Form 
          value={this.state.location} 
          change={this.handleChangeInputValue} 
          click={this.handleGetForecastButtonClick}/>
          </div>
        </div>
        <WeatherPanel weather={this.state.weather} location={this.state.location}/>
      </div>
    );
  }
}

export default App;
